var _self, logRes , did,myInfo,product,address,pnumb,formdata
export default {
	data() {
		return {
			product: [],
			myInfo:[],
			pnumb:null
		};
	},
	onLoad(options) {
		_self = this;
				logRes = _self.checkLogin('../Buy/Buy',1);
				// _self.myInfo=uni.getStorageSync('myInfo');
				_self.product = JSON.parse(options.product);
				_self.pnumb = options.pnum/* uni.getStorageSync('OPnum') */;
				console.log(_self.pnumb);
				console.log(_self.product);
				_self.did = logRes[0];
				
				uni.request({
					url: _self.apiServer+'app/order/myinfo',
					method: 'GET',
					data: {
						did : _self.did
					},
					success: res => {
						_self.myInfo = res.data.data;
						console.log(_self.myInfo);
					},
					fail: () => {},
					complete: () => {}
				});
			
	        },
	methods: {
		formSubmit: function(e) {
		                // console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
		                formdata = e.detail.value 
						// console.log(formdata)

						uni.request({
							url: _self.apiServer+'app/order/buy',
							method: 'POST',
							header: {
								'content-type': 'application/x-www-form-urlencoded', 
							},
							data: {
								pid				:  formdata.pid,
								pnum			:  formdata.pnum,
								receiverid		:  formdata.receiverid,
								receiveraddress :  formdata.receiveraddress,
								receivername	:  formdata.receivername ,
								receiverphone   :  formdata.receiverphone,
								sendid			:  formdata.sendid

							},
							success: res => {
								uni.showToast({
									title:'操作成功',
									duration: 1000
								});
								
								setTimeout(function(){
									uni.reLaunch({
												    url: '../../product/product'
												});
									})
							},
							fail: () => {},
							complete: () => {}
						});
		            },
		retu(){
			uni.showToast({
				title:'操作成功',
				duration: 1000
			});
			
			setTimeout(function(){
				uni.reLaunch({
							    url: '../../product/product'
							});
				},1000)
		}
	}
}
